import React,{useEffect} from 'react';
import {connect} from 'react-redux';
import userIco from '../assets/img/ico/user.png';
import passIco from '../assets/img/ico/pass.png';

import '../assets/css/Login.css'

// import axios from 'axios'
// import HttpApi from '../assets/js/common'

const Login=(props)=>{
  let userName,passWord
  useEffect(()=>{
    user()
  })
  const user=()=>{ //判断是否登陆，后转跳主页
    let local=localStorage.getItem('user');
    if(local){
      props.history.push('./')
    }
  }

  const login=(userName,passWord)=>{
    if (!userName.value.trim()||!passWord.value.trim()) {
      return
    }
    const data={ //模拟网络请求后返回的数据
      userName:'张三',
      userID:123456,
    }
    localStorage.setItem('user',JSON.stringify(data))
    props.history.push('./')

    // axios.post(HttpApi.login,{})
    // .then(function (response) {
    // })
    // .catch(function (error) {
    // });
  }

  return(
  <div className="LoginModal">
    <div className="login">
      <h1>Note示例</h1>
      <form onSubmit={(e)=>{
        e.preventDefault()
        login(userName,passWord)
      }}>
        <div className="s_input">
          <span><img src={userIco} alt="ico"/></span>
          <input type="text" placeholder="请输入用户名" ref={node=>(userName=node)}/>
        </div>
        <div className="s_input">
          <span><img src={passIco} alt="ico"/></span>
          <input type="password" placeholder="请输入密码" ref={node=>(passWord=node)}/>
        </div>
        <div className="s_bunbox">
          <button type="button">注册</button>
          <button type="submit">登录</button>
        </div>
      </form>
    </div>
  </div>
)}

export default connect()(Login);
