export const menu =(state={menuone:[],menutwo:{}},action)=>{
  switch(action.type){
    case 'getMenu':
    return({
      menuone:action.menuone,
      menutwo:action.menutwo,
    });
    default:
    return state
  }
}
export const check=(state='KING',action)=>{
  switch(action.type){
    case 'setCheck':
    return(action.check);
    default:
    return state
  }
}