import React,{useState,useEffect,Component} from 'react';
import './font-awesome-4.7.0/css/font-awesome.min.css'
import './notebook.css';
const $ = require('jquery')

export const Button=({aria="true",ico,type,className,text,onClick,style})=>{
  let nclass
  className?nclass=' '+className:nclass='';
  switch(type){
    case 'vari_1':
    return(
      <button className={'nButton vari_1'+nclass} onClick={onClick} style={style}>
        <i className={"fa "+ico} aria-hidden={aria}/>
        <span>{text}</span>
      </button>
    )
    default:
    return(
      <button className={'nButton initial'+nclass} onClick={onClick} style={style}>
        <span>{text}</span>
      </button>
    )
  }
}

export const Select=(props)=>{
  const [seles,setSeles]=useState(false)
  const [value,setValue]=useState()
  const [icon,setIcon]=useState('')
  const [show,setShow]=useState('')

  const porpv=()=>{
    const aPromise=new Promise((resolve,reject)=>{
      for(let i=0;i<props.list.length;i++){
        if(props.list[i].value==props.value){
          resolve(props.list[i].text)
          break;
        }
      }
      reject('请选择')
    })
    aPromise.then((val)=>{
      setValue(val)
      setSeles(true)
    },(err)=>{
      setValue(err)
      setSeles(false)
    })
  }
  const Clik=(e)=>{
    e.stopImmediatePropagation()
    setShow('')
  }
  const clickP=(e)=>{
    e.nativeEvent.stopImmediatePropagation()
    if(show==''){
      setShow(' show')
    }else{
      setShow('')
    }
  }
  const remove=(e)=>{
    e.nativeEvent.stopImmediatePropagation()
    if(seles){
      setValue('请选择')
      setSeles(false)
      setShow('')
      props.onSelect({})
    }else if(show==''){
      setShow(' show')
    }else{
      setShow('')
    }
  }
  useEffect(()=>{
    porpv()
  },[])
  useEffect(()=>{
    document.addEventListener('click',Clik)
    return ()=>{document.removeEventListener('click',Clik)}
  },[show])

  return(
    <div className={"nselect"+show+' '+seles} style={{width:"200px",}}>
      <p className={"f selecti"} onClick={(e)=>clickP(e)}>{value}</p>
      <i className={"sclear fa fa-chevron-down remo"+icon} onClick={(e)=>remove(e)}></i>
      <ul>
        {props.list.map((e,i)=>{
          return(
            <li className="f" key={i} onClick={()=>{
              setShow('')
              setValue(e.text)
              setSeles(true)
              props.onSelect(e)
            }}>{e.text}</li>
          )
        })}
      </ul>
    </div>
  )
}

export const Modal=(props)=>{
  const vist=props.visibital
  return(
    <div></div>
  )
}

export class LTable extends Component{
  constructor(props:any) {
    super(props);
    this.resize=this.resize.bind(this)
    this.state = {
      id:this.props.id||'l_'+(new Date().getTime()+Math.random()).toFixed(3),
      clas:this.props.className||'',
      height:this.props.height||'300px',
    }
  }
  componentDidMount() {
    window.addEventListener('resize', this.resize)
    this.resize()
  }
  componentDidUpdate(){
    this.resize()
  }
  componentWillUnmount(){
    window.removeEventListener('resize',this.resize);
  }
  resize(){
    let id=this.state.id
    let tbody=this.props.tbody
    if(tbody.length>0){
      let ltable=document.getElementById(id).getElementsByTagName('table')
      let thead0=ltable[0].getElementsByTagName('thead')[0]
      let thead1=ltable[1].getElementsByTagName('thead')[0]
      let ltbody=document.getElementById(id).getElementsByClassName('l_table_body')[0]
      let th0=thead0.getElementsByTagName('th')
      let th1=thead1.getElementsByTagName('th')
      thead0.style.minWidth=thead1.offsetWidth+'px'
      ltable[0].style.width=ltable[1].offsetWidth+'px'
      for(let i=0;i<th1.length;i++){
        th0[i].style.width=th1[i].offsetWidth+'px'
      }
      ltbody.onscroll=function(e){
        let div=document.getElementById(id).getElementsByClassName('l_table_head')[0]
        div.scrollLeft=e.target.scrollLeft
      }
    }
  }
  render(){
    let thead=this.props.thead,tbody=this.props.tbody
    return (
    <div id={this.state.id} className={"l_table "+this.state.clas}>
      {this.props.title?
        <h3 className="l_table_title">{this.props.title}</h3>:''
      }
      <div className="l_table_head l_scroll_hide">
        <table>
          <thead>
            <tr>
            {thead.map((e:any,i:any)=>
              <th key={i}>{e.title}</th>
            )}
            </tr>
          </thead>
        </table>
      </div>
      <div className="l_table_body" style={{height:this.state.height}}>
        {tbody.length>0?
        <table>
          <thead>
            <tr>
            {thead.map((e:any,i:any)=>
              <th key={i}>{e.title}</th>
            )}
            </tr>
          </thead>
          <tbody>
            {tbody.map((tr:any,a:any)=>
              <tr key={a}>
              {thead.map((td:any,b:any)=>{
                if(td.render){
                  return <td key={b}>{td.render(tr)}</td>
                }else{
                  return <td key={b}>{tr[td.dataIndex]}</td>
                }
              })}
              </tr>
            )}
          </tbody>
        </table>:
        <div className="l_nodata">
          <span>No Data</span>
        </div>
        }
      </div>
    </div>
    )
  }
}
export const Table=(props)=>{
  const [id,setID]=useState(props.id||'l_'+new Date().getTime())
  const thead=props.thead,tbody=props.tbody
  const clas=props.className||''
  useEffect(()=>{
    resize()
    window.onresize=()=>{resize()}
  },[])
  const resize=()=>{
    if(tbody.length>0){
     let a=document.getElementById(id).getElementsByTagName('table')[1].getElementsByTagName('thead')[0]
     let b=document.getElementById(id).getElementsByTagName('table')[0].getElementsByTagName('thead')[0]
     let d=document.getElementById(id).getElementsByClassName('l_table_body')[0]
     let c=document.getElementById(id).getElementsByTagName('table')[0]
     let ac=a.getElementsByTagName('th')
     let bc=b.getElementsByTagName('th')
     b.style.minWidth=c.offsetWidth+'px'
     document.getElementById(id).getElementsByTagName('table')[0].style.width=document.getElementById(id).getElementsByTagName('table')[1].offsetWidth+'px'
     for(let i=0;i<ac.length;i++){
      bc[i].style.width=ac[i].offsetWidth+'px'
     }
     d.onscroll=(e)=>{
      let div=document.getElementById(id).getElementsByClassName('l_table_head')[0]
      div.scrollLeft=e.target.scrollLeft
     }
    }
  }
  return(
    <div id={id} className={"l_table "+clas}>
      <div className="l_table_head l_scroll_hide">
        <table>
          <thead>
            <tr>
            {thead.map((e)=>
              <th>{e.title}</th>
            )}
            </tr>
          </thead>
        </table>
      </div>
      <div className="l_table_body" style={{height:props.height}}>
        {tbody.length>0?
        <table>
          <thead>
            <tr>
            {thead.map((e)=>
              <th>{e.title}</th>
            )}
            </tr>
          </thead>
          <tbody>
            {tbody.map((tr)=>
              <tr>
              {thead.map((td)=>{
                if(td.render){
                  return <td>{td.render(tr)}</td>
                }else{
                  return <td>{td.title}</td>
                }
              })}
              </tr>
            )}
          </tbody>
        </table>:
        <div className="l_nodata">
          <span>No Data</span>
        </div>
        }
      </div>
    </div>

  )
}
export default {Button,Select,Table,LTable}