import React,{useEffect} from 'react';
import {connect} from 'react-redux';
import {GetMenu,SetCheck} from '../actions';
import RouterS from './RouterS';
import '../assets/css/Home.css'

const store=(state)=>(state)

function Home(props) {
  let menuone=[],menutwo=[]
  const getMenu=()=>{
    if(props.location.pathname.match(/\/boxed/gi)){
      props.dispatch(SetCheck('BOXED'))
    }else if(props.location.pathname.match(/\/king/gi)){
      props.dispatch(SetCheck('KING'))
    }
    menuone=[
      {
        name:'KING',
        url:'/king',
        id:0
      },{
        name:'BOXED',
        url:'/boxed',
        id:1
      },
    ]
    menutwo={
      KING:[{
        name:'king',
        url:'/king',
        id:0
      },{
        name:'sing',
        url:'/sing',
        id:1
      }],
      BOXED:[
      {
        name:'boxed',
        url:'/boxed',
        id:0
      }]
    }
    const data={
      menuone,
      menutwo,
    }
    props.dispatch(GetMenu(data))

  }
  const setCheck=(m)=>{
    const data=m.name
    props.history.push(m.url)
    props.dispatch(SetCheck(data))
  }
  useEffect(()=>{
    getMenu()
  },[])

  return (
    <div className="HomeModal">
      <div className="header">
        <p className="logo"></p>
        <div className="head_left">
          {props.menu.menuone?props.menu.menuone.map((e)=>(
            <h3 da={props.check} onClick={()=>{setCheck(e)}} className={e.name==props.check?'check':''} key={e.id} data={e.url}>{e.name}</h3>
          )):''}
        </div>
        <div className="head_righ">
          <span className="call">退出</span>
          <span className="name">name</span>
        </div>
      </div>
        <div className="conter">
            <div className="c_Left">
              <ul>
              {props.menu.menutwo[props.check]?
                props.menu.menutwo[props.check].map((e)=>{
                  const an=new RegExp(e.url,"gi")
                  return(
                    <li onClick={()=>{props.history.push(e.url)}} className={props.location.pathname.match(an)?'check':''} key={e.id} data={e.url}>{e.name}</li>
                  )
                }):''}
              </ul>
            </div>
            <div className="c_right">
              <RouterS/>
            </div>
        </div>
    </div>
  );
}

export default connect(store)(Home);
